footer {
  padding: 5px;
}

.footer-icons > ul {
  list-style: none;
  color: green;
  display: flex;
  justify-content: space-around;
}

.icon-link {
  color: #1dbf73;
  padding: 1px;
}

.icon-link > i {
  width: 20px;
  size: 30px;
}

.footer-content {
  margin: 20px 0;
  text-align: center;
}

@media screen and (min-width: 1000px) {
  .footer-icons > ul {
    justify-content: center;
  }

  .icon-link {
    margin-right: 30px;
  }
}
