.card {
  box-shadow: -1px 1px 8px 3px rgba(0, 0, 0, 0.2);
  width: 95%;
  margin: 50px auto 30px auto;
  border-radius: 5px;
  border-bottom: 5px solid #1dbf73;
}

.card-image > img {
  object-fit: cover;
  object-position: center center;
  width: 100%;
  display: block;
  height: 300px;
  margin: auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.card-data {
  padding: 10px;
}

.card-title,
.card-description,
.card-footer {
  width: 95%;
  margin: 5px auto 0 auto;
  padding: 5px 0;
}
.card-title > p {
  margin-bottom: 3px;
  color: #1dbf73;
}

.card-description {
  line-height: 1.5;
  word-spacing: 3px;
  letter-spacing: 0.6px;
}

.icons {
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.icons > li {
  margin-right: 5px;
  color: black;
}
