.hero-heading > h1 {
  font-size: clamp(30px, 8vw, 40px);
}

.hero-container {
  min-height: 100vh;
}

.hero-heading {
  line-height: 1.4;
  /* word-spacing: 5px; */
}

.hero-heading > p {
  word-spacing: 6px;
  color: #1dbf73;
  font-weight: 600;
  margin-bottom: 10px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-heading > .second-heading {
  color: #999;
  width: 80%;
}

.hero-content {
  margin-top: 20px;
  color: #62646a;
  word-spacing: 5px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-button {
  margin-top: 50px;
  text-align: center;
  border: 2px solid #1dbf73;
  border-radius: 5px;
  width: 140px;
  font-size: 14px;
  font-weight: 600;
  color: #1dbf73;
  cursor: pointer;
}

.hero-button:hover a {
  background-color: rgba(29, 191, 115, 0.1);
  /* color: white; */
}

.hero-button > a {
  text-decoration: none;
  color: #1dbf73;
  display: inline-block;
  padding: 18px;
  width: 100%;
  transition: all 0.1s ease-in;
}

@media screen and (max-width: 420px) {
  .hero-container {
    /* margin-top: 100px; */
    padding: 100px 0;
  }
}

@media screen and (max-width: 330px) {
  .hero-container {
    margin-top: 50px;
    padding: 0;
  }
}

@media screen and (min-width: 760px) {
  main {
    margin-top: -30px;
  }
  .hero-container {
    padding-top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    margin: auto;
  }
  .hero-heading > p {
    font-size: clamp(16px, 3vw, 50px);
  }

  .hero-heading > h1 {
    font-size: clamp(30px, 7vw, 70px);
  }
}

@media screen and (min-width: 1000px) {
  main {
    margin: -40px auto auto auto;
    width: 90%;
  }

  .hero-heading > p {
    font-size: clamp(16px, 1.5vw, 50px);
  }
  .hero-heading > h1 {
    font-size: clamp(30px, 4.5vw, 70px);
  }

  .hero-content {
    width: 70%;
    font-size: clamp(16px, 1.5vw, 50px);
  }

  .hero-heading > .second-heading {
    width: initial;
  }
}

@media screen and (min-width: 1800px) {
  main {
    width: 80%;
  }

  .hero-content > p {
    font-size: clamp(16px, 1vw, 50px);
    width: 70%;
  }
}

@media screen and (min-width: 2000px) {
  main {
    width: 70%;
  }

  /* .hero-content > p {
    font-size: clamp(16px, 1vw, 50px);
    width: 70%;
  } */
}
