@media screen and (min-width: 760px) {
  .projects-heading {
    font-size: clamp(20px, 3vw, 30px);
  }

  .card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .card {
    width: 45%;
  }
}

@media screen and (min-width: 1000px) {
  #projects-nav {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }

  .projects-heading {
    font-size: clamp(16px, 1.5vw, 50px);
  }

  .card {
    width: 40%;
  }
}

@media screen and (min-width: 1400px) {
  /* .card-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      } */
  .card {
    width: 30%;
  }
}
