.contact {
  padding: 90px 0;
}

.contact > div {
  text-align: center;
  /* line-height: 2; */
}

.contact-container > h4 {
  margin-bottom: 12px;
  color: #1dbf73;
}

.contact-container > h2 {
  margin-bottom: 12px;
  /* color: #1dbf73; */
}

.contact-container > p {
  line-height: 1.5;
  word-spacing: 5px;
  text-align: center;
}

.contact-container-button > a {
  display: block;
  text-decoration: none;
  color: #1dbf73;
  border: 2px solid #1dbf73;
  width: clamp(100px, 35vw, 180px);
  margin: 40px auto 0 auto;
  padding: 15px 15px;
  border-radius: 5px;
  transition: all 1s ease-in;
}

.contact-container-button > a:hover {
  /* display: inline-block; */
  background-color: rgba(29, 191, 115, 0.1);
}

@media screen and (min-width: 760px) {
  .contact-container {
    width: 80%;
    margin: auto;
  }

  .contact-container > h4 {
    font-size: clamp(16px, 2.5vw, 25px);
  }

  .contact-container > h2 {
    font-size: clamp(20px, 3.5vw, 30px);
  }
}

@media screen and (min-width: 1000px) {
  .contact {
    width: 80%;
    margin: auto;
  }
}
