.navigation {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  height: 40px;

  /* padding: 0 20px; */
}

.mobile-menu {
  position: relative;
  height: 18px;
  width: 30px;
}

.logo > h2 {
  font-weight: bold;
  color: #1dbf73;
  font-size: 23px;
}

.logo {
  position: relative;
  width: 160px;
}

.logo::after {
  content: "";
  height: 28px;
  width: 3px;
  background-color: #1dbf73;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  /* visibility: hidden; */
}

.mobile-menu > .bar-one {
  height: 3px;
  width: 25px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-menu > .bar-two {
  height: 3px;
  width: 20px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 5px;
  right: 0;
  border-radius: 1px;
}

.mobile-menu > .bar-three {
  height: 3px;
  width: 15px;
  background-color: #1dbf73;
  position: absolute;
  right: 0;
  top: 10px;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-navigation {
  position: absolute;
  border-bottom: 2px solid #1dbf73;
  width: 100vw;
  top: 80px;
  left: 0;
  text-align: center;
  background-color: white;
  transform: scale(0);
  transform-origin: top;
  transition: transform 0.2s;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 2px 2px 0 #62646a;
}

.mobile-navigation > .nav-links {
  list-style: none;
  text-decoration: none;
}

.nav-links a {
  text-decoration: none;
  color: #62646a;
  font-weight: 700;
}
.nav-links {
  line-height: 2;
  padding: 10px 0;
}

@-webkit-keyframes blink {
  from {
    background-color: #1dbf73;
  }
  to {
    background-color: white;
  }
}

@keyframes blink {
  from {
    background-color: #1dbf73;
  }
  to {
    background-color: white;
  }
}

@media screen and (min-width: 760px) {
  nav {
    margin-top: 10px;
  }
}

@media screen and (min-width: 1000px) {
  nav {
    padding-bottom: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
  }

  .mobile-menu {
    display: none;
  }

  .mobile-navigation {
    position: static;
    position: initial;
    background-color: transparent;
    background-color: initial;
    box-shadow: none;
    transform: scale(1);
    border-radius: 0;
    border-radius: initial;
    border: medium none currentColor;
    border: initial;
    width: auto;
    width: initial;
  }
  .navigation-stick {
    width: 50%;
  }
  .nav-links {
    display: flex;
    justify-content: space-around;
  }

  .nav-links a {
    padding: 5px;
  }
  .nav-links > li:hover a {
    border-bottom: 3px solid #1dbf73;
  }
}

