.left-bar,
.right-bar {
  display: none;
}

@media screen and (min-width: 1000px) {
  .left-bar {
    position: fixed;
    bottom: 0;
    left: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .social-icons-container {
    list-style: none;
  }

  .social-icons-container > li {
    margin-bottom: 20px;
    transition: transform 0.2s ease-in;
  }

  .social-icons-container > li:hover a {
    color: #1dbf73;
  }

  .social-icons-container > li:hover {
    transform: translateY(-3px);
  }

  .social-icons-container a {
    text-decoration: none;
    color: #999;
  }

  .line-one {
    display: block;
    background-color: #999;
    height: 100px;
    width: 2px;
  }

  .right-bar {
    position: fixed;
    right: -43px;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
  }

  .right-bar-mail > a {
    text-decoration: none;
    color: #999;
  }

  .right-bar > .right-bar-mail {
    position: relative;
    bottom: 110px;
    transform: rotate(90deg);
    transform-origin: center center;
  }

  .right-bar-mail p {
    font-size: 13px;
    transition: transform 0.2s ease-in;
  }

  .line-two {
    display: inline-block;
    background-color: #999;
    height: 100px;
    width: 2px;
  }

  .right-bar-mail:hover p {
    color: #1dbf73;
    transform: translateX(-3px);
  }
}

@media screen and (min-width: 2000px) {
  .left-bar {
    left: 200px;
  }

  .right-bar {
    right: 100px;
  }

  .right-bar-mail > p {
    font-size: 16px;
  }

  .right-bar > .right-bar-mail {
    bottom: 130px;
  }
}

